.video {
  padding: 17px 0 34px;
  position: relative;
  z-index: 1;
  color: white;
  @include media-breakpoint-up(md) {
    padding-top: 20px;
  }
  @include media-breakpoint-up(xl) {
    padding-top: 37px;
    padding-bottom: 37px;
  }

  &::after {
    @include pseudo;
    position: absolute;
    top: 0;
    right: -15px;
    bottom: 0;
    left: -15px;
    z-index: -1;
    background: $color-black url('#{$img}/pexels.webp') no-repeat center center;
    background-size: cover;
    @include media-breakpoint-up(xss) {
      width: 100vw;
      right: 0;
      left: 50%;
      transform: translateX(-50%);
    }
  }

  &__header {
    max-width: 1040px;
    margin: 0 auto 15px;
    text-align: center;
  }

  &__subheading {
    margin: 0 -15px 6px;
    font-size: 17px;
    line-height: normal;
    font-weight: bold;
    color: $color-red;
    text-shadow: 0 1px 5px rgba(black, .4);
    @include media-breakpoint-up(xl) {
      margin: -2px 0 15px;
      font-size: 27px;
      line-height: 30px;
    }
  }

  &__heading {
    text-shadow: 0 10px 6px rgba(black, .1);
    @include media-breakpoint-up(md) {
      font-size: 34px;
      line-height: 38px;
    }
    @include media-breakpoint-up(xl) {
      font-size: 51px;
      line-height: 53px;
      &.big {
        font-size: 65px;
        line-height: 68px;
      }
    }

    u {
      text-decoration: underline;
      text-decoration-thickness: .05em;
      text-underline-offset: .125em;
    }
  }

  &__player {
    height: 577px;
    border: 3px solid #aaa;
    margin: 0 auto;
    position: relative;
    z-index: 2;
    background: white;
    @include media-breakpoint-up(md) {
      height: 440px;
    }
    @include media-breakpoint-up(lg) {
      height: 520px;
    }
    @include media-breakpoint-up(xl) {
      max-width: 1020px;
      height: 586px;
    }
  }

  &__play {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    &::before {
      width: 271px;
      height: 271px;
      margin: auto;
      display: block;
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background: url('#{$img}/play.svg') no-repeat center center;
      background-size: contain;
      animation: zoom-in-zoom-out 1.1s ease-in-out infinite;
      -webkit-backface-visibility: hidden;
      @include media-breakpoint-up(lg) {
        width: 360px;
        height: 360px;
      }
      @include media-breakpoint-up(xl) {
        width: 457px;
        height: 457px;
      }
    }
  }
}

@keyframes zoom-in-zoom-out {
  0% {transform: scale(1);}
  40% {transform: scale(1.05);}
  100% {transform: scale(1);}
}